import * as $ from "jquery";

export class TildaPaykeeper {

    server_url = 'https://severyane.server.paykeeper.ru/create/';

    constructor() {
        this.init();
    }

    init() {
        this.bindAjax();
    }

    bindAjax() {
        // console.log('bind Ajax');
        $( document ).ajaxComplete(( event, xhr, settings ) => {
            this.onProcessComplete(xhr, settings);
        });
    }

    onProcessComplete(xhr: JQuery.jqXHR, settings: JQuery.AjaxSettings) {
        if (settings.url == 'https://forms.tildacdn.com/procces/')
        {
            const data = xhr.responseJSON;
            if (('message' in data) && (data.message == "OK")
                && ('results' in data) && (data.results.length > 0))
            {
                const [results = ''] = data.results;
                const [, order_id] = results.match(/^\d+:\d+:[A-Z\d]+-(\d+)$/);
                const requestData = JSON.parse('{"' + settings.data.replace(/&/g, '","').replace(/=/g,'":"') + '"}', function(key, value) { return key===""?value:decodeURIComponent(value) })
                // console.log('onProcessComplete', xhr, settings, data, requestData);
                const tildaPayment = JSON.parse(requestData.tildapayment)
                let amount = tildaPayment.amount;
                if (('delivery' in tildaPayment) && ('price' in tildaPayment.delivery))
                    amount += tildaPayment.delivery.price;

                this.followPay(order_id);
                // this.submitAmount(amount, order_id, requestData.Email, requestData.Phone);
                //console.log('tildaPayment', tildaPayment);
            }
        }

    }

    followPay(order_id: string) {
        window.location.replace('https://pay.delivery.severyane.moscow/order/' + order_id);
    }

    submitAmount(amount, order_id, client_email, client_phone) {
        let newForm = $('<form action="' + this.server_url + '" method="post"></form>')
            .append($('<input>').attr('name','service_name').attr('value', 'Оплата заказа <'
            + order_id + '> в ресторане Северяне.'))
            .append($('<input>').attr('name','sum').attr('value', amount))
            .append($('<input>').attr('name', 'orderid').attr('value', ''+order_id))
            .append($('<input>').attr('name','client_email').attr('value', ''+client_email))
            .append($('<input>').attr('name','client_phone').attr('value', ''+client_phone))
        ;

        newForm.appendTo('body').submit();
        // console.log('submitAmount', newForm.html());
    }
}
